/**
 * Community Page JavaScript
 *
 * This file contains the JavaScript for the Community page.
 */

// Import components
import Accordion from '../components/accordion';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Community page initialized');
  
  // Initialize accordion if there are any on the page
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Set up community join form validation if it exists
  const joinForm = document.querySelector('.community-join-form');
  if (joinForm) {
    setupFormValidation(joinForm);
  }
}

/**
 * Set up form validation
 * @param {HTMLElement} form - The form element
 */
function setupFormValidation(form) {
  form.addEventListener('submit', function(event) {
    let isValid = true;
    
    // Get form fields
    const emailField = form.querySelector('input[type="email"]');
    
    // Validate email if it exists
    if (emailField && !isValidEmail(emailField.value)) {
      isValid = false;
      showError(emailField, 'Please enter a valid email address');
    }
    
    // If form is not valid, prevent submission
    if (!isValid) {
      event.preventDefault();
    }
  });
}

/**
 * Check if email is valid
 * @param {string} email - Email to validate
 * @returns {boolean} - True if valid, false otherwise
 */
function isValidEmail(email) {
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}

/**
 * Show error message for a field
 * @param {HTMLElement} field - The field with error
 * @param {string} message - Error message
 */
function showError(field, message) {
  // Remove any existing error message
  const existingError = field.parentElement.querySelector('.error-message');
  if (existingError) {
    existingError.remove();
  }
  
  // Add error class to field
  field.classList.add('error');
  
  // Create and append error message
  const errorElement = document.createElement('div');
  errorElement.className = 'error-message';
  errorElement.textContent = message;
  field.parentElement.appendChild(errorElement);
  
  // Remove error when field is focused
  field.addEventListener('focus', function() {
    field.classList.remove('error');
    const error = field.parentElement.querySelector('.error-message');
    if (error) {
      error.remove();
    }
  });
}

// Initialize when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
